import React, { Component } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

const CoverImg = styled.img `
  width: 100%;
  object-fit: cover;
  transition-duration: 0.5s;
  margin: 0;
`;

export default CoverImg;



